import { ESBoolean, ESUndefined } from '../runtime/primitiveTypes';
import { types } from "../constants";
export default function load(context) {
    const globalConstants = {
        'false': new ESBoolean(false),
        'true': new ESBoolean(true),
        'nil': new ESUndefined(),
        'Any': types.any,
        'Number': types.number,
        'String': types.string,
        'Bool': types.bool,
        'Func': types.function,
        'Array': types.array,
        'Object': types.object,
        'Type': types.type,
        'Error': types.error,
        'Undefined': types.undefined
    };
    for (let constant in globalConstants) {
        const value = globalConstants[constant];
        context.set(constant, value, {
            global: true,
            isConstant: true
        });
    }
}
